import React, { useEffect, useState } from "react"
import PropTypes from "prop-types"
import Axios from "../../axios"
import { BASE_URL } from "../../config"
import { FaFacebook } from "@react-icons/all-files/fa/FaFacebook"
import { FaTwitter } from "@react-icons/all-files/fa/FaTwitter"
import { FaLinkedin } from "@react-icons/all-files/fa/FaLinkedin"
import { FaYoutube } from "@react-icons/all-files/fa/FaYoutube"
import { FaInstagram } from "@react-icons/all-files/fa/FaInstagram"

import ficon1 from "../images/common/ficon-1.svg"
import ficon2 from "../images/common/ficon-2.svg"
import ficon3 from "../images/common/ficon-3.svg"
import ficon4 from "../images/common/ficon-4.svg"
import ficon5 from "../images/common/ficon-5.svg"

import mail from "../images/common/mail.png"
import map from "../images/common/map.png"
import phone from "../images/common/phone-call.png"
import logo from "../images/common/logo.png"
import mapimage from "../images/mapimage.png"
import {Link,graphql, useStaticQuery,navigate } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"



const query = graphql`
    {
        allStrapiLogo {
          nodes {
            img {
              localFile {
                childImageSharp {
                  original {
                    src
                  }
                }
              }
            }
          }
        }

        allStrapiContactInfo {
          nodes {
            phone
            location
            email
          }
        }

        allStrapiSocialLinks {
          nodes {
            facebook
            instagram
            linkedin
            twitter
            youtube
          }
        }

        allStrapiCertificate {
          nodes {
            name
            img {
              localFile {
                childImageSharp {
                  original {
                    src
                  }
                }
              }
            }
          }
        }

        allStrapiCompanyInfo {
          nodes {
            info
          }
        }


    }
`

function Footer(props) {

  const data = useStaticQuery(query)
  const logoInfo = data.allStrapiLogo.nodes[0]
  const companyInfo = data.allStrapiCompanyInfo.nodes[0]
  const certificateInfo= data.allStrapiCertificate.nodes[0]

  const sociallinksInfo = data.allStrapiSocialLinks.nodes[0]
  const contactInfo = data.allStrapiContactInfo.nodes[0]

  


  // const [ContactInfo, setContactInfo] = useState({})
  // const [SocialLinks, setSocialLinks] = useState({})
  // const [Certificate, setCertificate] = useState({})
  // const [CompanyInfo, setCompanyInfo] = useState({})
  // const [logo, setLogo] = useState({})

  // useEffect(() => {
  //   async function fetchData() {
  //     const contact_info = await Axios.get("/contact-info")
  //     const social_links = await Axios.get("/social-links")
  //     const certificate = await Axios.get("/certificate")
  //     const company_info = await Axios.get("/company-info")
  //     const logo = await Axios.get("/logo")

  //     setContactInfo(contact_info.data)
  //     setSocialLinks(social_links.data)
  //     setCertificate(certificate.data)
  //     setCompanyInfo(company_info.data)
  //     setLogo(logo.data)
  //     return
  //   }
  //   fetchData()
  // }, [])

  return (
    <footer id="footer">
      <section className="container-fluid bg-msar-xdark bg-msar-xdark1">
        <section className="row ">
          <section className="container">
            <section className="row m-4 m-md-5">
              <section className="col-md-3">
                <img className="img-fluid " src={logoInfo.img.localFile.childImageSharp?.original.src} width="85%" />

                <p className="my-3">{companyInfo?.info}</p>
                {/* <img src={ficon1} />
                <img src={ficon2} />
                <img src={ficon3} />
                <img src={ficon4} />
                <img src={ficon5} /> */}

                <div class="social">
                  <ul class="ul-social">
                    {sociallinksInfo?.facebook ? (
                      <li>
                        <a href={sociallinksInfo?.facebook}>
                          <FaFacebook />
                        </a>
                      </li>
                    ) : null}

                    {sociallinksInfo?.instagram ? (
                      <li>
                        <a href={sociallinksInfo?.instagram}>
                          <FaInstagram />
                        </a>
                      </li>
                    ) : null}
                    {sociallinksInfo?.linkedin ? (
                      <li>
                        <a href={sociallinksInfo?.linkedin}>
                          <FaLinkedin />
                        </a>
                      </li>
                    ) : null}
                    {sociallinksInfo?.youtube ? (
                      <li>
                        <a href={sociallinksInfo?.youtube}>
                          <FaYoutube />
                        </a>
                      </li>
                    ) : null}
                    {sociallinksInfo?.twitter ? (
                      <li>
                        <a href={sociallinksInfo?.twitter}>
                          <FaTwitter />
                        </a>
                      </li>
                    ) : null}
                  </ul>
                </div>
              </section>
              <section className="col-md-3">
                <h6 className="text-uppercase">Quick Links</h6>
                <ul type="none" className="footer-link">
                  <li>
                    <a href="#" className="text-decoration-none">
                      <Link to="/">Home</Link>
                    </a>
                  </li>
                  <li>
                    <a href="#" className="text-decoration-none">
                      <Link to="/about">About Us</Link>
                    </a>
                  </li>
                  <li>
                    <a href="#" className="text-decoration-none">
                      <Link to="/product">Products</Link>
                    </a>
                  </li>
                  {/* <li>
                    <a href="#" className="text-decoration-none">
                      <Link to="/service">Services</Link>
                    </a>
                  </li> */}
                  <li>
                    <a href="#" className="text-decoration-none">
                      <Link to="/contact">Contact Us</Link>
                    </a>
                  </li>
                </ul>
              </section>
              <section className="col-md-3">
                <h6 className="text-uppercase">Contact Us</h6>
                <div className="icon-box">
                  <div className="icon mt-2 mr-1 ml-0">
                    <img src={mail} className="w-75" />
                  </div>
                  <div className="icon-details small">
                    <div className="icon-details-text">Email</div>
                    <div className="icon-details-value">
                      <a style={{color:"inherit"}} href={`mailto:${contactInfo?.email}`}>{contactInfo?.email}</a>
                    </div>
                  </div>
                </div>
                <div className="icon-box">
                  <div className="icon mt-2 mr-1 ml-0">
                  <img src={phone} className="w-75" />
                   
                  </div>
                  <div className="icon-details small">
                    <div className="icon-details-text">Location</div>
                    <div className="icon-details-value">
                      {contactInfo?.location}
                    </div>
                  </div>
                </div>
                <div className="icon-box">
                  <div className="icon mt-2 mr-1 ml-0">
                  <img src={map} className="w-75" />
                  </div>
                  <div className="icon-details small">
                    <div className="icon-details-text">Phone</div>
                    <div className="icon-details-value">
                      {contactInfo?.phone}
                    </div>
                  </div>
                </div>
              </section>
              <section className="col-md-3">
                <h6 className="text-uppercase">{certificateInfo?.name}</h6>
                <img
                  src={certificateInfo.img.localFile.childImageSharp?.original.src}
                  className="img-fluid mt-2 footer-image"
                  width="85%"
                />
              </section>
            </section>
          </section>
        </section>
      </section>
      <section className="container-fluid bg-msar-xxdark">
        <section className="row">
          <section className="container">
            <section className="row m-4">
              <section className="col-md-6">
                <div className="text-md-left text-center mb-3 mb-md-0">
                   Copyright 2021. All Rights Reserved.
                </div>
              </section>
              <section className="col-md-6">
                <div className="ext-md-right text-center">
                  Design And Developed by{" "}
                  <a href="https://www.srashtasoft.com/">Srashtasoft.</a>
                </div>
              </section>
            </section>
          </section>
        </section>
      </section>
    </footer>
  )
}

Footer.propTypes = {}

export default Footer

{
  /* <p>{contactInfo?.email}</p>
      <p>{SocialLinks?.youtube}</p>
      <p>{Certificate?.name}</p>
      {console.log(Certificate?.img?.url)}
      <img src={`http://localhost:1337${Certificate?.img?.url}`} alt="text" /> */
}
