import React, { useState, useEffect } from "react"

import {Link,graphql, useStaticQuery,navigate } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import PropTypes from "prop-types"
import Axios from "../../axios"
import { BASE_URL } from "../../config"
import Loadable from "@loadable/component"
import "owl.carousel/dist/assets/owl.carousel.css"
import "owl.carousel/dist/assets/owl.theme.default.css"
const OwlCarousel = Loadable(() => import("react-owl-carousel"))

const query = graphql`
    {
      allStrapiClients {
        nodes {
          img {
            localFile {
              childImageSharp {
                original {
                  src
                }
              }
            }
          }
        }
      }

    }
`

function Clients(props) {
  const { isHome } = props

  const data = useStaticQuery(query)
  const clients = data.allStrapiClients.nodes

  // const [Clients, setClients] = useState([])

  // useEffect(() => {
  //   async function fetchData() {
  //     const request = await Axios.get("/clients")
  //     setClients(request.data)
  //     return request
  //   }
  //   fetchData()
  // }, [])
  const responsive = {
    0: {
      items: 2,
      nav: true,
      loop: true,
    },
    600: {
      items: 4,
      nav: true,
      loop: true,
    },
    1000: {
      items: 7,
      nav: true,
      loop: true,
    },
  }

  return (
    <section id="client-logo bg-colors">
      <section className="container-fluid">
        <section className="row">
          <section
            className={isHome ? "col-md-12 mb-md-5 pb-md-5" : "col-md-12"}
          >
            {Object.keys(clients).length > 0 ? (
              <OwlCarousel
                className="owl-carousel client-logo owl-theme m-5"
                loop
                nav
                responsive={responsive}
                autoplay={true}
                autoplayTimeout={3000}
              >
                {/* <div className="owl-carousel owl-theme msar-carousel"> */}
                {clients?.map(document => (
                  <div className="item">
                    <img
                      className
                      style={{ objectFit: "contain" }}
                      height={60}
                      // src={`${BASE_URL}${document?.img?.url}`}
                      src={document.img.localFile.childImageSharp?.original.src}
                    />
                  </div>
                ))}
              </OwlCarousel>
            ) : (
              <></>
            )}
          </section>
        </section>
      </section>
    </section>
  )
}

Clients.propTypes = {}

export default Clients
