import React, { useState, useEffect, useRef } from "react"
import { FaBars } from "@react-icons/all-files/fa/FaBars"
import PropTypes from "prop-types"
import Axios from "../../axios"
import { BASE_URL } from "../../config"
// import $ from "jquery"
import $ from "jquery"
import mail from "../images/common/mail.svg"
import map from "../images/common/map.svg"
import phone from "../images/common/phone-call.svg"

// import logo from "../images/common/logo.png"

import { FaLinkedin } from "@react-icons/all-files/fa/FaLinkedin"
import 'font-awesome/css/font-awesome.min.css';
import {Link,graphql, useStaticQuery,navigate } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"


const query = graphql`
    {
        allStrapiProducts {
          nodes {
            name
            id
            product_titles {
              name
              slug
              id
            }
            
          }
        }

        allStrapiServiceCategories {
          nodes {
            name
            id
            slug
          }
        }

        allStrapiLogo {
          nodes {
            img {
              localFile {
                childImageSharp {
                  original {
                    src
                  }
                }
              }
            }
          }
        }

    }
`

function Nav() {

  const data = useStaticQuery(query)
  const productsInfo = data.allStrapiProducts.nodes
  const servicesInfo = data.allStrapiServiceCategories.nodes
  const logoInfo = data.allStrapiLogo.nodes[0]
  

  const menuRef = useRef()


  const onClickProductTitle = _id => {
    // alert("Hello")
    // alert(menuRef.current.classList)
    // menuRef.current.classList.remove('dropdown-menu')
    navigate(`/product/${_id}/`)
  }

  const onClickService = _id => {
    console.log("clicked jd", _id)
    navigate(`/service/${_id}`)
  }
  
  const [isScrollButtonHide, setIsScrollButtonHide] = useState(false)
  const [isNavbarFixed, setIsNavbarFixed] = useState(false)

  useEffect(() => {
    let btn = document.getElementById("scrollbutton")
    var num = 100;
    $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
          setIsScrollButtonHide(false)
        } else {
          setIsScrollButtonHide(true)
        }

        if ($(window).scrollTop() > num) {
          // $('.nav').addClass('nav-fixed');
          setIsNavbarFixed(true)
        }else{
            // $('.nav').removeClass('nav-fixed');
            setIsNavbarFixed(false)
          }
      });

    btn.addEventListener('click', (e) => {
      e.preventDefault();
      $('html, body').animate({scrollTop:0}, '300');
    });

  })

  return (
  
      <header>
         <a id="scrollbutton" className={isScrollButtonHide ? "": "show"}></a>
<nav className={isNavbarFixed ? "navbar navbar-expand-lg navbar-dark bg-tanspherent fixed-top": "navbar navbar-expand-lg navbar-dark bg-tanspherent"}>
  <Link className="navbar-brand" to="/"> <img
                      src={logoInfo.img.localFile.childImageSharp?.original.src}
                      className="img-fluid logo-image"
                       height={85}
                    /></Link>
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>

  <div className="collapse navbar-collapse" id="navbarSupportedContent">
    <ul className="navbar-nav ml-auto">
      <li className="nav-item">
      <Link className="nav-link" to="/">
                            Home
                          </Link>
      </li>
      <li className="nav-item">
      <Link className="nav-link" to="/about/">
                            About Us
                          </Link>
      </li>
      <li className="nav-item dropdown">
        <a className="nav-link dropdown-toggle " href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
        Products
        </a>
        <div ref={menuRef} className="dropdown-menu" aria-labelledby="navbarDropdown">

          
          <div className="mains-container">
            <div className="row">

              {productsInfo.map((document,i) => (
                   <>
                      <div className="col-md-3">
                        <ul className="nav flex-column">
                        <span  key={i} className="text-uppercase text-white">{document.name}</span>
                        {document.product_titles.map((item, i) => (
                            <li key={item.id} style={{cursor:"pointer"}}
                            onClick={() =>
                              onClickProductTitle(item.slug)
                            } >
            
                            
                                <a className="nav-link navlink-a" ><span className="nav-boders">{item.name}</span></a>
                              
                            </li>
                        ))}
                      </ul>
                      </div>
                   </>
              ))}
           
           
            
            </div>
          </div>
        


        </div>
      </li>
      <li className="nav-item dropdown">
        <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
        Services
        </a>
        <div className="dropdown-menu service-menu" aria-labelledby="navbarDropdown">


          <div className="mains-container">
            <div className="row">
              <div className="col-md-4">
               
                <ul className="nav flex-column">

                  {servicesInfo.map((document,i) => (
                      <li className="nav-item" key={document.id} style={{cursor:"pointer"}}
                        onClick={() => onClickService(document.slug)}>
                          <a className="nav-link" > <span className="nav-boders">{document.name}</span></a>
                      </li>
                  ))}
               
              </ul>
              </div>
            </div>
          </div>
        </div>
      </li>
      <li className="nav-item">
        
      <Link className="nav-link" to="/contact/">
          Contact Us
        </Link>
      </li>

    </ul>
   
  </div>
</nav>      
   </header>
  )
}

Nav.propTypes = {}

export default Nav
